body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-content: center;
          align-content: center;
  text-align:center;
  padding: 50px;
  -webkit-align-items: center;
          align-items: center;
}

.filter {
  height: 300px;
}
.Space {
  position: fixed;
  top: 0;
  background-color: white;
  width: 100%;
  height: 50px;
}

.topMenu {
  position: fixed;
  top: 0;
  left: 70px;
  right: 50px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: white;
  height: 60px;
  padding-left: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.topItem {
  font-family: "default";

  margin-left: 20px;
  margin-right: 20px;
  padding-top: 10px;
}

.Title {
  font-family: "default";
  margin-right: 20px;

}

.Container {
  margin-left: 330px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  height: 100%;
}

.singleCol {
  margin-left: 20px;
  width: 100%;
  height: 100%;
  /*background-color: antiquewhite;*/
}

.singleCol > img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@font-face {
  font-family: default;
  src: url(/Intergem_v2/static/media/Crique.5d5ce2c5.woff);
}

.Font {
  font-family: "default";
  color: #282c34;
}

.Header {
  padding-top: 30px;
  padding-top: 40px;
  font-family: "default";
  color: aliceblue;
}

.Contact {
  font-family: "default";
  border-top: aliceblue;
  border-top-style: solid;
  border-top-width: 1px;
  padding-top: 10px;
  color: aliceblue;
  margin-left: 30px;
  margin-right: 30px;
}

.Item {
  font-family: "default";
  margin-bottom: 15px;
  color: aliceblue;
}

.Category {
  margin-bottom: 0;
  -webkit-align-self: center;
          align-self: center;
  font-family: "default";
  color: aliceblue;
}

.container-fluid {
  height: 100vh;
  border-color: white;
  border-width: 20px;
  border-style: solid;
}

.Menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: fixed;
  top: 50px;
  bottom: 0px;
  width: 300px;
  /*flex: 0 0 300px;*/
  background-color: #282c34;
  color: aliceblue;
}

.IMG {
  max-width:100%;
  height: auto;
  margin-bottom: 10px;
}

.Author {
  font-family: "default";
  position: absolute;
  bottom: -55px;
  left: 140.93px;
  font-size: 10pt;
}

.Content {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: stretch;
          align-items: stretch;
}


.Blue {
  color: white;
  background-color: #282c34;
}

.row {
  margin-left: 30px;
  margin-right: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.marginTop {
  margin-top: 50px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

